<template>
  <div style="background: rgba(0, 0, 0, 0.36)">
    <Card style="width:100%;background: rgba(0, 0, 0, 0.36);border: none;border-bottom: white 1px solid">
      <Icon type="ios-bulb"/>
      说明
      <span style="color: #ffabfa">角色管理</span>
    </Card>
    <Tabs style="color:white;font-weight: bold">
      <TabPane label="角色列表" icon="logo-apple">
        <Card :bordered="false"
              style="width: 100%;padding:0px;background: rgba(0, 0, 0, 0.36);margin-top: -15px;">
          <div style="margin:-5px 10px 10px 0px">
            <Button icon="md-add" type="default" size="small" shape="circle" style="float: right"
                    @click="saveOrUpdateroleInfoForm('add',{type:'1'})" ghost>增加
            </Button>
            <Input v-model="roleInfoGrid.keyword" placeholder="搜索关键字" style="width: auto;width: 500px">
              <Button @click="searchRoleList" slot="append" icon="ios-search"></Button>
            </Input>
          </div>
        </Card>
        <Table ref="table"
               style="background: rgba(0, 0, 0, 0.36);"
               :height="clientHeight"
               :columns="roleInfoGrid.columns"
               :data="roleInfoGrid.data" showStripe>
          <template slot-scope="{ row }" slot="name">
            <Row>
              <Col span="4" v-if="row.type==2">&nbsp;</Col>
              <Col span="8" v-if="row.type==3">&nbsp;</Col>
              <Col :span="16">
                <div style="color: rgba(13,30,13,0.58);border-radius: 20px"
                     :style="{color:function(){
                              if(row.type==2){return '#2d8cf0'}
                              else if(row.type==3){return 'green'}
                              else{return 'yellow'}
                          }()}">
                  <Icon size="30" :type=row.icon></Icon>&nbsp;&nbsp;
                  <strong>{{ row.name }}</strong>&nbsp;&nbsp;
                </div>
              </Col>
            </Row>
          </template>
          <template slot-scope="{ row,index }" slot="action">
            <div style="text-align: right">
              <Icon size="25" style="color: lightblue" type=md-eye @click="showRoleView(index)"></Icon>
              <Icon size="25" style="color: yellow" type=ios-people @click="openRoleUserWin(row)"></Icon>
              <Icon size="25" style="color: green" type=ios-create
                    @click="saveOrUpdateroleInfoForm('update',{type:row.type,id:row.id})"></Icon>
              <Icon size="25" style="color: red" type=ios-trash @click="removeRole(row.id)"></Icon>
            </div>
          </template>
        </Table>
        <div style="margin: 10px;">
          <div style="float: right;">
            <Page style="height: 40px" show-total show-elevator show-sizer
                  :total="roleInfoGrid.pageData.total"
                  :current="roleInfoGrid.pageData.current"
                  :page-size="roleInfoGrid.pageData.size"
                  @on-change="changePageNum"
                  @on-page-size-change="changePageSize"
            />
          </div>
        </div>
      </TabPane>
      <TabPane label="Windows" icon="logo-windows">
      </TabPane>
      <TabPane label="Linux" icon="logo-tux">标签三的内容</TabPane>
    </Tabs>
    <!--修改添加-->
    <Drawer draggable
            :closable="false"
            :mask-closable="false"
            v-model="roleInfoForm.show"
            width='1000' >

      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="roleInfoForm.show=false"/>
        <div class="ivu-drawer-header-inner">{{roleInfoForm.title}}</div>
        <div class="" style="position: absolute;right: 20px;top:10px;">
          <Button type="primary" @click="saveOrUpdateRole('roleInfoForm')">提交</Button>
        </div>
      </div>

      <Tabs value="name1">
        <TabPane label="基本信息" name="name1">
          <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
            <Form ref="roleInfoForm" :model="roleInfoForm.data"
                  :rules="roleInfoForm.validate" :label-width="80">
              <FormItem label="名称" label-position="left" prop="name">
                <Input class="" v-model="roleInfoForm.data.name"
                       placeholder=""/>
              </FormItem>
              <FormItem label="代码" label-position="left" prop="code">
                <Input class="" v-model="roleInfoForm.data.code"
                       placeholder=""/>
              </FormItem>
              <FormItem label="图标" label-position="left" prop="icon">
                <Input class="" v-model="roleInfoForm.data.icon"
                       placeholder=""/>
              </FormItem>
            </Form>
          </Card>
        </TabPane>
        <TabPane label="角色菜单" name="name2">
          <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
            <Tree ref="roleMenuTree" show-checkbox
                    :data="menuTreeData"
                    style="margin-top:0px;"></Tree>
          </Card>
        </TabPane>
      </Tabs>
    </Drawer>
    <!--详情-->
    <Drawer v-model="roleInfoForm.roleView.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width="1000">

      <!--头部 start-->
      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="roleInfoForm.roleView.show=false"/>
        <div class="ivu-drawer-header-inner">详情</div>
      </div>
      <!--头部 end-->

      <Tabs value="name1">
        <TabPane label="基本信息" name="name1">
          <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
             <Row type="flex" v-for="(item ,index) in roleInfoGrid.columns"
               v-if="item.key"
               :key="index"
               style="height: 40px;margin-top: 5px;background: rgba(4,9,7,0.2);line-height: 35px;border-radius: 6px">
            <Col span="4">
              <div style="color: rgb(167,225,230);padding: 5px;text-align: center;font-weight: bold">
                {{item.title}}
              </div>
            </Col>
            <Col span="20">
              <div style="color: rgba(159,216,209,0.65);padding: 5px;">
                <div v-if="item.key=='icon'">
                  <Icon size="30" :type="roleInfoForm.roleView.data[item.key||item.slot]"/>
                </div>
                <div v-else>
                    <span v-if="roleInfoForm.roleView.data[item.key||item.slot]" style="color: yellow">
                      {{(roleInfoForm.roleView.data[item.key||item.slot])}}
                    </span>
                  <span v-else>
                </span>
                </div>
              </div>
            </Col>
          </Row>
          </Card>
        </TabPane>
        <TabPane label="角色菜单" name="name2">
          <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
            <Tree ref="roleMenuTree" :data="menuTreeData"
                  style="margin-top:0px;"></Tree>
          </Card>
        </TabPane>
        <TabPane label="角色用户" name="name3">
          <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
            <div class="demo-avatar">
            <Avatar icon="ios-person" />
            <Avatar>U</Avatar>
            <Avatar>USER</Avatar>
            <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" />
            <Avatar style="color: #f56a00;background-color: #fde3cf">U</Avatar>
            <Avatar style="background-color: #87d068" icon="ios-person" />
          </div>
          </Card>
        </TabPane>
      </Tabs>
    </Drawer>

    <RoleAddUser v-on:roleAddUserListen="listener"
                 :role="roleInfoForm.selectRole"
                 :clientHeight="clientHeight"
                 :show="roleInfoForm.roleAddUser" />
  </div>
</template>

<script>
  import IconSelect from "../../components/IconSelect";
  import RoleAddUser from "../../components/RoleAddUser";
  export default {
    components: {RoleAddUser, IconSelect},
    props: ["clientHeight"],
    data() {
      return {
        menuTreeData: [],
        styles: {
          height: 'calc(100% - 55px)',
          overflow: 'auto',
          paddingBottom: '53px',
          position: 'static'
        },
        roleInfoGrid: {
          keyword:"",
          columns: [
            {
              type: 'selection',
              width: 60,
              align: 'center',
              fixed: 'left',
            },
            {
              title: '操作',
              slot: 'action',
              width: 150,
              fixed: 'left',

            },
            {
              title: '名称',
              key: 'name',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '代码',
              key: 'code',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '图标',
              key: 'icon',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '创建人',
              key: 'createUserIdText',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '创建时间',
              key: 'createTime',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '修改人',
              key: 'updateUserIdText',
              //width: 200,
              //fixed: 'left'
            },
            {
              title: '修改时间',
              key: 'updateTime',
              //width: 200,
              //fixed: 'left'
            }
          ],
          data: [],
          pageData: {
            total: 100,
            current: 1,
            size: 10
          }
        },
        roleInfoForm: {
          title: "添加",
          iconSelect: false,
          roleMenuRelation:[],
          roleAddUser:null,
          selectRole:{},
          showField: {
            id: false,
            name: false,
            code: false,
            createUserId: false,
            createTime: false,
            updateUserId: false,
            updateTime: false,
            icon: false,
          },
          select: {
            pid: []
          },
          clearData: {
            id: '',
            name: '',
            code: '',
            createUserId: '',
            createTime: '',
            updateUserId: '',
            updateTime: '',
            icon: '',
          },
          data: {
            id: '',
            name: '',
            code: '',
            createUserId: '',
            createTime: '',
            updateUserId: '',
            updateTime: '',
            icon: '',
          },
          validate: {
            name: [
              {required: true, message: '请输入名称', trigger: 'blur'},
            ],
            code: [
              {required: true, message: '请输入代码', trigger: 'blur'},
            ],
            icon: [
              {required: true, message: '请输入图标', trigger: 'blur'},
            ],
          },
          show: false,
          roleView: {
            data: {},
            show: false
          }
        }
      }
    },
    methods: {
      openRoleUserWin(role){
        this.roleInfoForm.roleAddUser = true
        this.roleInfoForm.selectRole = role
      },
      searchMenuTree(roleMenuRelation) {
        const that = this;
        that.$util.req("/api/menuService/findAllMenuTreeView")
          .setSuccessfn(function (result) {
            if (result.code === 1000) {
              that.menuTreeData = []
              that.menuTreeData = that.treeMenuAddRender(result.data,roleMenuRelation,true)
            }
          })
          .setErrorfn(function (e) {
          })
          .go(that)
      },
      treeMenuAddRender(menuList,roleMenuRelation,expand) {
        for (let i in menuList) {
          if(roleMenuRelation.indexOf(menuList[i].id)>-1){
            menuList[i].mark=true
            menuList[i].checked=true
          }
          menuList[i].expand = expand
          menuList[i].render = this.treeIcon
          if (menuList[i].children.length > 0) {
            this.treeMenuAddRender(menuList[i].children,roleMenuRelation)
          }
        }
        return menuList
      },
      treeIcon: (h, {root, node, data}) => {
        let mark="";
        if(data.mark){
          mark="................[选中️]";
        }
        return h('span', {
          style: {
            display: 'inline-block',
            width: '100%',
            color: function () {
              if (data.type == '2') {
                return '#2d8cf0'
              } else if (data.type == '3') {
                return 'green'
              } else {
                return 'yellow'
              }
            }()
          }
        }, [
          h('span', [
            h('Icon', {
              props: {
                type: data.icon,
                size: 30
              },
              style: {
                marginRight: '8px'
              }
            }),
            h('span', data.name+mark)
          ])
        ])
      },
      showRoleView(index) {
        this.roleInfoForm.roleView.show = true;
        this.roleInfoForm.roleView.data = this.roleInfoGrid.data[index]
        this.loadMenuTree(this.roleInfoGrid.data[index].id)
      },
      removeRole(id) {
        const that = this;
        that.$Modal.confirm({
          title: "警告",
          content: "确定要删除吗？",
          onOk: function () {
            that.$util.req("/api/roleService/delRole")
              .setJsonDataStr({ids: id})
              .setSuccessfn(function (result) {
                if (result.success) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'Role删除成功'
                  });
                  that.searchRoleList()
                } else {
                  that.$Notice.error({
                    title: '操作失败',
                    desc: '${cfg.UpModuleName}删除失败'
                  });
                }
              }).go(that)
          }
        })
      },
      changeType(value, callBack) {
        callBack = callBack || function () {
        }
        if (value == "1") {
          this.roleInfoForm.showField.pid = false
          this.roleInfoForm.showField.viewPath = false
          this.roleInfoForm.showField.groupName = false
          this.roleInfoForm.data.pid = '0'
          callBack()
        } else if (value == "2" || value == "3") {
          if (value == "2") {
            this.roleInfoForm.showField.groupName = true
          }
          if (value == "3") {
            this.roleInfoForm.showField.groupName = false
          }
          this.roleInfoForm.showField.pid = true
          this.roleInfoForm.showField.viewPath = true
        }
      },
      listener: function (fun) {
        fun(this)
      },
      saveOrUpdateroleInfoForm(actiontype, data) {
        if (actiontype === "add") {
          this.roleInfoForm.title = "添加"
          this.roleInfoForm.data = this.roleInfoForm.clearData
          this.searchMenuTree([])
        } else if (actiontype === "update") {
          this.roleInfoForm.title = "修改"
          this.loadMenuTree(data.id)
        }
        this.roleInfoForm.show = true
      },
      loadMenuTree(roleId){
        const that = this;
        that.$util.req("/api/roleService/searchRoleById")
          .setJsonDataStr({id: roleId})
          .setSuccessfn(function (result) {
            if(result.success){
              let roleInfo = result.data;
              that.roleInfoForm.data = roleInfo;
              that.roleMenuRelation = result.data.menuIds;
              that.searchMenuTree(result.data.menuIds)
            }
          })
          .go(that)
      },
      saveOrUpdateRole(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            const that = this;
            let menus=that.$refs.roleMenuTree.getCheckedAndIndeterminateNodes()
            let menu_checks=that.$refs.roleMenuTree.getCheckedNodes()
            let roleMenuIds=[];
            let menu_checks_id=[];
            for(let i in menu_checks){
              menu_checks_id.push(menu_checks[i].id)
            }
            for(let i in menus){
              roleMenuIds.push(menus[i].id)
              roleMenuIds.push(menus[i].type)
              if(menu_checks_id.indexOf(menus[i].id)>-1){
                roleMenuIds.push('1')//全选中状态
              }else{
                roleMenuIds.push('0')//半选中状态
              }
            }

            that.roleInfoForm.data.menuIds=roleMenuIds
            that.$util.req("/api/roleService/saveOrUpdateRole")
              .setJsonDataStr(that.roleInfoForm.data)
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'role' + that.roleInfoForm.title + '成功'
                  });
                  that.searchRoleList();
                  that.roleInfoForm.show = false;
                }
              })
              .setErrorfn(function (e) {})
              .go(this)
          } else {
            //this.$Message.error('Fail!')
          }
        })
      },
      searchRoleList() {
        const that = this;
        that.$util.req("/api/roleService/searchRoleList")
          .setJsonDataStr({
            start: that.roleInfoGrid.pageData.current,
            size: that.roleInfoGrid.pageData.size,
            keyword:that.roleInfoGrid.keyword
          })
          .setSuccessfn(function (result) {
            if (result.success) {
              that.roleInfoGrid.data = result.data.records;
              that.roleInfoGrid.pageData.current = result.data.current;
              that.roleInfoGrid.pageData.total = result.data.total;
            }
          })
          .setErrorfn(function (e){})
          .go(that)
      },
      changePageNum(pageNum) {
        this.roleInfoGrid.pageData.current = pageNum;
        this.searchRoleList()
      },
      changePageSize(pageSize) {
        this.roleInfoGrid.pageData.size = pageSize;
        this.searchRoleList()
      }
    },
    mounted() {
      this.searchRoleList();
    }
  }
</script>

<style>

</style>



